<template>
    <div v-if="isVariableDefined">
        <slot></slot>
      <script>
        blocks = document.querySelectorAll('.page');
        blocks.forEach(function(block) {
            block.innerHTML = block.innerHTML.replace(/\%(.*?)\%/g, function(_, match) {
            // Your custom JavaScript goes here
            console.log(match)
            if (match === '(.*?)\\')
                return match
            return globalPyodide.runPython('from tupi import Noun; '+ match); // For example, convert the text to uppercase
            });
        });
      </script>
    </div>
    <div v-else>
    </div>
  </template>
  
  <script>
  export default {
    data() {
        return {
            isVariableDefined: false
        };
    },
    mounted() {
        const checkVariableInterval = setInterval(function() {
            if (window.pyodideLoaded) {
            this.isVariableDefined = true;
            clearInterval(checkVariableInterval);
            }
        }, 100); // Check every 100 milliseconds
    }
  }
  </script>